$backdrop-color: rgba(0, 0, 0, 0.5);

.backdrop {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  right: 0;
  top: 0;
  background: $backdrop-color;
  opacity: 0;
  visibility: hidden;
  z-index: 99999;
  transition: opacity 150ms ease-in-out, visibility 150ms ease-in-out;
  pointer-events: none;

  &.open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transition: opacity 150ms ease-in-out, visibility 150ms ease-in-out;
  }
}

// Для центрированной модалки
.backdrop[data-animation="center"] {
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
}

.container {
  padding: 24px;
  background: white;
  transition: none;
}

// Для анимации rightSide через data-атрибуты
.container[data-animation="rightSide"] {
  position: fixed;
  inset: 0 0 0 auto;
  width: 280px;
  max-width: 80vw;
  height: 100vh;
  background: white;
  z-index: 10000;
  transform: translateX(100%);
  transition: transform 300ms ease, opacity 300ms, visibility 300ms;
  opacity: 0;
  visibility: hidden;
  overflow: auto;
  box-shadow: -2px 0 8px 0px rgba(0, 0, 0, 0.25);
}

.container[data-animation="rightSide"][data-open="true"] {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}

// Для анимации center через data-атрибуты
.container[data-animation="center"] {
  position: relative;
  background: white;
  border-radius: 4px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  margin: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 24px;
}

.title {
  margin: 0;
  font-weight: 500;
  color: #0d2344;
}

.closeButton {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  cursor: pointer;
  transition: transform 0.2s ease;

  &:hover {
    transform: scale(1.1);
  }
}

.closeIcon {
  font-size: 32px;
  font-weight: 300;
  line-height: 1;
  color: #ef8e01;
  user-select: none;
}

